import React, { useState, useEffect, useRef } from "react";
import "../Css/Account.css";
import Header from "../../../Component/Header/Header";
import Footer from "../../../Component/Footer/Footer";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import axios from "axios";
import { Button } from "react-bootstrap";
import { TextField } from "@material-ui/core";
import { useForm } from "react-hook-form";
import { useSnackbar } from "notistack";
import {updatePassword} from "../../../apis/account";
const userLogin = JSON.parse(localStorage.getItem("userLogin") || "[]");
function UpdatePassword() {
  const [passwordOld, setPasswordOld] = useState("");
  const [rePassword, setRepassword] = useState("");
  const [newPassword, setNewpassword] = useState("");
  const [notification, setNotification] = useState("");
  const [error, setError] = useState("");
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
  } = useForm({});
  const Password = useRef({});
  Password.current = watch("newPassword", "");
  const handleUpdatePassword = (e) => {
    const data = {
      oldPassword: e.old_password,
      password: e.newPassword,
    };
    updatePassword(data)
      .then((response) => {
        setNotification(response.message);
      })
      .catch((err) => {
        setError(err.response.message);
      });
  };
  return (
    <>
      <Header />
      <div className="noindex">
        <div className="container">
          <div className="bg-white" style={{ padding: "10px" }}>
            <div id="content">
              <table id="tb-account" style={{ width: "100%" }}>
                <tbody>
                  <tr>
                    <td id="account-left">
                      <dl>
                        <dt>n hng t mua</dt>
                        <dd>
                          <Link to="/account-order">H cha thanh ton</Link>
                        </dd>
                        <dd>
                          <Link to="/account-ordered">
                            n hng  thanh ton
                          </Link>
                        </dd>
                        <dd>
                          <Link to="/account-completeOrder">
                            n hng  hon thnh
                          </Link>
                        </dd>
                        <dd>
                          <Link to="/account-orderCanceled">
                            {" "}
                            n hng  hy
                          </Link>
                        </dd>
                      </dl>
                      <dl>
                        <dt>Thng tin ti khon</dt>
                        <dd>
                          <Link to="/updateUser">Thng tin c nhn</Link>
                        </dd>
                        <dd>
                          <Link to="/updatePassword">Thay i mt khu</Link>
                        </dd>
                      </dl>
                    </td>
                    <td valign="top" className="tb-order">
                      <h3>Thay i mt khu</h3>
                      <p style={{ color: "#1f4cb1" }}>
                        {notification ? <b>Thng bo: {notification}</b> : ""}
                      </p>
                      <p style={{ color: "red" }}>
                        {error ? <b>Thng bo: {error}</b> : ""}
                      </p>
                      <div className="input-group-updatePassword">
                        <TextField
                          id="outlined-basic-1"
                          variant="outlined"
                          name="old_password"
                          type="password"
                          label="Nhp mt khu c"
                          {...register("old_password", { required: true})}
                        />
                          {errors?.old_password?.type === "required" && (
                          <p>Mt khu khng c b trng</p>)}
                      </div>
                      <div className="input-group-updatePassword">
                        <TextField
                          id="outlined-basic-2"
                          variant="outlined"
                          name="newPassword"
                          type="password"
                          label="Nhp mt khu mi"
                          {...register("newPassword", { required: true, pattern: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/})}
                        />
                         {errors?.newPassword?.type === "required" && (
                           <p>Mt khu khng c b trng</p>)}
                           {errors?.newPassword?.type === "pattern" && (
                           <p>Mt khu c 8 k t v 1 k t hoa</p>)}
                      </div>
                      <div className="input-group-updatePassword">
                        <TextField
                          id="outlined-basic-3"
                          variant="outlined"
                          name="re_password"
                          type="password"
                          label="Nhp li mt khu mi"
                          {...register("re_password", { validate: value => value === Password.current || "Mt khu khng khp"
                        })}
                        />
                        {errors.re_password && <p>{errors.re_password .message}</p>}

                      </div>

                      <div>
                        <Button
                          variant="primary"
                          className="btn-updateUser"
                          type="submit"
                          onClick={handleSubmit(handleUpdatePassword)}
                        >
                          Thay i
                        </Button>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default UpdatePassword;
